import './main.scss'

const removeFileExtension = (str) => str.replace(/\.(png|jpe?g|svg)$/, '');

const toCamelCase = (str) => str.replace(/(?:^\w|[A-Z]|\b\w)/g, (ltr, idx) => idx === 0 ? ltr.toLowerCase() : ltr.toUpperCase()).replace(/(\s|\W)+/g, '');

const convertPaths = (imagePaths) => {
  let images = {};
  Object.keys(imagePaths).forEach(key => images[toCamelCase(removeFileExtension(key))] = imagePaths[key]);
  return images;
}

const imagePaths = require('../assets/images/*.png');

// Tell Parcel this JS file uses the images in ../assets/images
// image names are converted to 'camelCase' and file suffixes are removed
const images = convertPaths(imagePaths)

const { Elm } = require('./Main.elm');

Elm.Main.init({
  node: document.querySelector('main'),
  flags: images
});
